import { Component, OnInit, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import { Role } from 'src/app/models/role.model';
import { NgbModalRef, NgbModal, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';

@Component({
  selector: 'app-role-details',
  templateUrl: './role-details.component.html',
  styleUrls: ['./role-details.component.scss']
})
export class RoleDetailsComponent  implements OnInit, AfterViewInit {
  message: string;
  role: Role;
  attributes: string[];

  @ViewChild('content') content: ElementRef<HTMLElement>;
  private modal: NgbModalRef;

  constructor(private modalService: NgbModal, private router: Router) {
  }

  ngOnInit(): void {
    this.role = history.state.resource;

    if (!this.role)
    {
      this.ok();
    }
    this.attributes = Object.keys(this.role);
    // tslint:disable-next-line: no-string-literal
    this.message = 'Details of role: ' + this.role['name'];
  }

  ngAfterViewInit(): void {
    const options: NgbModalOptions = {
      size: 'md',
      backdrop: 'static',
    };
    this.modal = this.modalService.open(this.content, options);
  }

  ok(): void {
    this.router.navigateByUrl('/roles');
    if (this.modal)
    {
      this.modal.dismiss();
    }
  }
}
