import { Component, OnInit } from '@angular/core';
import { Role } from 'src/app/models/role.model';
import { ResourceDeleteContext } from 'src/app/shared/dashboard-resource/resource-delete/resource-delete.component';

@Component({
  selector: 'app-role-delete',
  templateUrl: './role-delete.component.html',
  styleUrls: ['./role-delete.component.scss']
})
export class RoleDeleteComponent implements OnInit {
  context: ResourceDeleteContext<Role>;

  constructor() { }

  ngOnInit(): void {
    this.context = {
      deleteUrl: 'rolesDeleteUrl',
      data: history.state.resource,
      title: 'Delete Role Resource!',
      description: 'Are you sure to delete this role?'
    };
  }

}
